import uniRequest from 'uni-request';
import {
    mapMutations  
} from 'vuex';
export default {
	data() {
		return {
			emailText:'去验证',
			userInfo:{
				username:'',
				mobile:'',
				email:'',
				email_active:false
			},
		}
	},
	onLoad() {
		this.getDate()
	},
	methods: {
		...mapMutations(['logout']),
		async getDate() {
			await uniRequest({
				url: '/user/',
				method: 'get',
				headers: {
					Authorization: 'JWT ' + uni.getStorageSync('userInfo').token
				},
			}).then(res => {
				console.log(res)
				this.userInfo = res.data
			}).catch(error => {
				console.log(error);
			})
		},
		
		// 验证
		async toTest(){
			if(this.emailText === '去验证'){
				await uniRequest({
					url: '/email/',
					method: 'put',
					data:{
						email: this.userInfo.email
					},
					headers: {
						Authorization: 'JWT ' + uni.getStorageSync('userInfo').token
					},
				}).then(res => {
					console.log(res)
					if(res.status === 200){
						this.emailText = '已发送'
					}
				}).catch(error => {
					console.log(error);
				})
			}
		},

		//提交
		confirm() {
			if (!this.userInfo.username) {
				this.$api.msg('请填写用户名');
				return;
			}
			if (!/(^1[3|4|5|7|8][0-9]{9}$)/.test(this.userInfo.mobile)) {
				this.$api.msg('请填写手机号码');
				return;
			}
			
			if (!this.userInfo.email) {
				this.$api.msg('请填写电子邮箱');
				return;
			}
			
			
			uniRequest({
				url: '/users/update/info/',
				method: 'put',
				data:this.userInfo,
				headers: {
					Authorization: 'JWT ' + uni.getStorageSync('userInfo').token
				},
			}).then(res => {
				console.log(res)
				if(res.status === 200){
					this.$api.msg('资料修改成功')
					this.logout()
					setTimeout(() => {
						uni.switchTab({
							url:'/pages/user/user',
						})
					}, 800)
				}else if(res.status === 400){
					this.$api.msg(res.data.username[0])
					return
				} 
			}).catch(error => {
				console.log(error);
				this.$api.msg('资料修改失败')
			})
			
			
		},
	}
}
